@import "Colors";

.btn {
    padding: 8px 20px;
    border-radius: 4px;
    outline: none;
    border: none;
    cursor: pointer;
    white-space: nowrap;

    &--primary {
        background-color: $primary;
        color: $secondary;
        border: 1px solid $primary;
    }

    &--secondary {
        background-color: transparent;
        color: $primary;
        padding: 8px;
        border: 1px solid $primary;
        transition: all 0.3s ease-in-out;
    }
    &--medium {
        padding: 8px 20px;
        font-size: 1.125rem;
    }
    &--large {
        padding: 12px 26px;
        font-size: 1.25rem;
    }
    &--mobile {
        text-align: center;
        border-radius: 4px;
        width: 80%;
        text-decoration: none;
        font-size: 1.5rem;
        background-color: transparent;
        color: $primary;
        padding: 14px 20px;
        border: 1px solid $primary;
        transition: all 0.3s ease-out;
        overflow: hidden;
    }
    &--wide {
        padding: 12px 64px;
        font-size: 1.25rem;
    }

    &--medium:hover, &--large:hover, &--mobile:hover {
        transition: all 0.3s ease-out;
        background: $primary;
        color: $secondary;
    }
    &--wide:hover {
        color: $primary;
        background-color: $tertiary;
        transition: all 0.2s ease-out;
    }
    &-link {
        display: flex;
        justify-content: center;
        align-items: center;
        text-decoration: none;
        padding: 8px 16px;
        height: 100%;
        width: 100%;
    }
}
.primary {
    background-color: $secondary;
    color: $primary;
    border: none;

    &:hover {
        background-color: $primary;
        color: $secondary;
        border: none;
    }
}
.blue {
    background-color: $quaternary;
    color: $primary;
    border: none;
}


