@import "../../components/Colors.scss";
@import "../../components/MediaQueries.scss";

.sign-in {
    padding: 160px 0;
    display: flex;
    justify-content: center;
    align-content: center;
    margin: 0;
    outline: 0;

    &__wrapper {
        display: flex;
        flex-direction: row;
        align-items: start;
        margin: 0 auto;
        background-color: $secondary;
        color: $primary;
        max-width: 700px;
        width: 100%;
        border-radius: 1.1rem;
    }
    &-side-1 {
        max-width: 30%;
        width: 100%;
    }
    &-side-2{
        max-width: 70%;
        width: 100%;
        height: 100%;
    }
    &-logo {
        color: $primary;
        display: flex;
        justify-content: center;
        margin: 60% auto;
        width: 100%;
        height: 100%;
        cursor: pointer;
        font-size: 7rem;
    }
    &__heading {
        font-size: 1.9rem;
        margin: 4% 0;
        text-align: center;
    }
    &__form {
        width: 100%;
        max-width: 400px;
        margin: 0 auto;
    }
    &__input {
        width: 100%;
        padding: 8px 10px;
        border-radius: 2px;
        outline: none;
        border: none;
        font-size: 18px;
        margin: 15px 0;
        border: 1px solid $primary;
    }
    &__label {
        font-size: 1.2rem;
        display: flex;
    }
    &__text {
        margin: 5% 0;
    }
}

@include sign-in-small-devices {
    .sign-in {
        padding-right: 4%;
        padding-left: 4%;
        
        &__wrapper {
            flex-direction: column;
            padding: 5% 5%;
        }
        &-logo { 
            margin: 0;
            font-size: 4rem;
        }
        &-side-1 {
            max-width: 100%;
        }
        &-side-2{
            max-width: 100%;
        }
        &__heading {
            font-size: 25px;
        }
        &__input {
            font-size: 14px;
        }
        &__label {
            font-size: 18px;
        }
    }
}